import React from "react";

const sampleContracts = [
  { id: 1, title: "Hp ng chm sc di hn", period: "1 nm" },
  { id: 2, title: "Hp ng chm sc ngn hn", period: "1 thng" },
  { id: 3, title: "Hp ng dch v y t", period: "6 thng" },
];

const Contracts = () => {
  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-2xl font-semibold mb-6">Hp ng</h1>
      <div className="grid grid-cols-1 md:grid-cols-2 gap-6">
        {sampleContracts.map((c) => (
          <div key={c.id} className="p-4 bg-white rounded shadow">
            <h3 className="text-lg font-medium">{c.title}</h3>
            <p className="text-sm text-gray-600">Thi hn: {c.period}</p>
            <div className="mt-4">
              <button className="px-3 py-1 bg-indigo-600 text-white rounded">Chi tit</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Contracts;
