import React from "react";
import { useParams, useHistory } from "react-router-dom";

const data = {
  1: { id: 1, name: "Nguyn Vn Hng", age: 30, exp: "6 nm", bio: "Bc s chuyn khoa vi nhiu nm kinh nghim chm sc ngi cao tui." },
  2: { id: 2, name: "Nguyn Vn Hng", age: 30, exp: "6 nm", bio: "Bc s chuyn khoa vi nhiu nm kinh nghim chm sc ngi cao tui." },
};

const DoctorDetails = () => {
  const { id } = useParams();
  const history = useHistory();
  const doc = data[id];

  if (!doc) return <div className="p-8">Bc s khng tn ti.</div>;

  return (
    <div className="container mx-auto px-4 py-8">
      <button onClick={() => history.goBack()} className="mb-4 text-blue-600">
         Quay li
      </button>
      <h2 className="text-2xl font-semibold mb-4">{doc.name}</h2>
      <p>Tui: {doc.age}</p>
      <p>Kinh nghim: {doc.exp}</p>
      <p className="mt-4">{doc.bio}</p>
    </div>
  );
};

export default DoctorDetails;
