import React from "react";

const sampleCertificates = [
  { id: 1, title: "Chng ch chm sc ngi cao tui", org: "Hc vin Y" },
  { id: 2, title: "Chng ch dinh dng lm sng", org: "Trung tm Dinh dng" },
  { id: 3, title: "Chng ch s cp cu", org: "Hi Ch thp " },
];

const Certificates = () => {
  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-2xl font-semibold mb-6">Chng ch</h1>
      <div className="space-y-4">
        {sampleCertificates.map((c) => (
          <div key={c.id} className="p-4 bg-white rounded shadow flex justify-between items-center">
            <div>
              <h3 className="text-lg font-medium">{c.title}</h3>
              <p className="text-sm text-gray-600">{c.org}</p>
            </div>
            <button className="px-3 py-1 bg-green-600 text-white rounded">Xem</button>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Certificates;
