import React from "react";

const sampleSkills = [
  { id: 1, title: "K nng hi sc", desc: "M t tm tt v k nng hi sc" },
  { id: 2, title: "K nng dinh dng", desc: "M t tm tt v dinh dng cho ngi cao tui" },
  { id: 3, title: "K nng giao tip", desc: "M t tm tt v giao tip v chm sc tinh thn" },
];

const Skills = () => {
  return (
    <div className="container mx-auto px-4 py-8">
      <h1 className="text-2xl font-semibold mb-6">K nng</h1>
      <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
        {sampleSkills.map((s) => (
          <div key={s.id} className="p-4 bg-white rounded shadow">
            <h3 className="text-lg font-medium mb-2">{s.title}</h3>
            <p className="text-sm text-gray-600">{s.desc}</p>
            <div className="mt-4">
              <button className="px-3 py-1 bg-blue-600 text-white rounded">Xem chi tit</button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default Skills;
